
body {
  margin: 0;
  padding: 0;
  font-size: 1.2em;
  letter-spacing: 0.03em;
  font-family: sans-serif;
}
.title {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
  background: #000;
  background-position: center;
}
i {
  position: absolute;
  z-index: 20;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #7929a7;
  opacity: 0.2;
}
.h1title {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  text-align: center;
  margin: 0 auto;
  z-index: 50;
  display: block;
  opacity: 0.8;
  font-size: 8vw;
  line-height: 100vh;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  text-shadow: 0 0 120px #000;
  color: #7929a7;
}
.h1title:first-child {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    pointer-events: none;
    -webkit-clip-path: polygon(0% 0%, 100% 0, 100% 30%, -65% 100%, 0% 100%);
            clip-path: polygon(0% 0%, 100% 0, 100% 30%, -65% 100%, 0% 100%);
}
.h1title:nth-child(2) {
    -webkit-clip-path: polygon(100% 0%, 100% 100%, -65% 100%, 100% 30%);
            clip-path: polygon(100% 0%, 100% 100%, -65% 100%, 100% 30%);
}



